.grid-main{
  display: grid;
  grid-template-columns: auto auto;  
  text-align: center;
  gap: 15px;
  margin-top: 50px;
  justify-content: center;
  align-items: center;
  padding: 10px;
}
.grid-child h3{
font-weight: 600;
margin-top: 10px;
}
.grid-child p{
  font-size: 15px;
}
.grid-child{
  border: 1px solid #4f8deb;
  border-radius: 15px;
  padding: 10px;
  cursor: pointer;
  /* height: 120px; */
}
.grid-child:hover{
  background-color: #4f8deb;
  color: whitesmoke;
}
.h1-ai{
  text-align: center;
  margin-top: 50px;
}
.ai-span{
  color: #4f8deb;
}


.textarea{
  font-size: 20px;
  margin-left: 23rem;
  margin-top: 6rem;
  padding: 10px;
}
.button1{
  width: 200px;
  height: 50px;
  font-size: 20px;
  background-color: #4f8deb;
  border-radius: 1rem;
  cursor: pointer;
  border: none;
  margin-left: 35rem;
  margin-top: 3rem;
  

}
.back{
  background-color: #4f8deb;
  color: whitesmoke;
  cursor: pointer;
  border: none;
  border-radius: 2rem;
  margin-left: 2rem;
  width: 100px;
  height: 30px;
}
@media (max-width:768px){
  .textarea{
    margin-left: 1rem;
  }
  .button1{
    margin-left: 18rem;
  }
}
@media(max-width:414px){
  .textarea{
    width: 22rem;
  }
  .button1{
    margin-left: 2.5rem;
  }
  .result-text{
    line-height: 1.5rem;
    /* display: inline; */
    font-size: 15px;
  }
}
@media(max-width:390px){
  .textarea{
    width: 20rem;
  }
  .button1{
    margin-left: 1rem;
  }
}
@media(max-width:375px){
  .textarea{
    width: 18rem;
  }
}
@media (max-width:360px){
  .button1{
    width: 11rem;
  }
}